import React from "react";
import { Row, Col } from "react-bootstrap";
import { IoAlbums } from "react-icons/io5";
import ProjectCard from "./ProjectCard";

const Project = () => {
  return (
    <div>
      <div class="d-flex align-items-start">
        <IoAlbums className="fs-1 mt-1" />
        <span className="fs-2 ms-2">Projects</span>
      </div>
      <Row className="mb-3 mt-2">
        <Col>
          <ProjectCard
            title="Pedestrian Counter"
            subtitle="Python, PyQt5, OpenCV, numpy"
            desc="App that counts pedestrians using SSD or YOLOv4 models and Centroid Tracker algorithm - thesis."
            sourceLink="https://github.com/Blatts12/PedestrianCounter"
          />
        </Col>
        <Col>
          <ProjectCard
            title="MeloDIE"
            subtitle="Python, PySide2, youtube-dl, pynput"
            desc="App that allows to listen to Youtube playlists."
            sourceLink="https://github.com/Blatts12/MeloDIE"
          />
        </Col>
      </Row>
      <Row className="mb-3">
        <Col>
          <ProjectCard
            title="Simple Blog"
            subtitle="Python, Django, Django Rest Framework, NodeJS, React, Redux, Bootstrap, Markdown"
            desc="Simple Blog with comments and accounts."
            sourceLink="https://github.com/Blatts12/simple-blog"
          />
        </Col>
        <Col>
          <ProjectCard
            title="Higher or Lower Youtube Edition"
            subtitle="Python, Django, Youtube Data API, jQuery, Bootstrap"
            desc="Higher or Lower game but with number of views from Youtube videos."
            sourceLink="https://github.com/Blatts12/higher-or-lower-yt-edition"
          />
        </Col>
      </Row>
    </div>
  );
};

export default Project;
