import React from "react";
import { Col, Row } from "react-bootstrap";
import { IoAlbums } from "react-icons/io5";
import ProjectCard from "./ProjectCard";

const ActiveProject = () => {
  return (
    <div>
      <div class="d-flex align-items-start">
        <IoAlbums className="fs-1 mt-1" />
        <span className="fs-2 ms-2">Active Projects</span>
      </div>
      <Row className="mb-3 mt-2">
        <Col>
          <ProjectCard
            title="Tcopy"
            subtitle="Python, Django, Django REST Framework, Django REST Knox, Djnago CORS Headers"
            desc="Backend for Tcopy (Twitter Copy)"
            sourceLink="https://github.com/Blatts12/tcopy"
            demoLink="https://tcopy-backend.herokuapp.com/api/feed/global/"
          />
        </Col>
        <Col>
          <ProjectCard
            title="Tcopy - web"
            subtitle="Vite, TypeScript, React, React Router, React Virtuoso, React Hook Form, React Icons, Redux, Redux Toolkit, SASS"
            desc="React frontend for Tcopy (Twitter Copy)"
            sourceLink="https://github.com/Blatts12/tcopy-web"
            demoLink="https://tcopy-web.vercel.app/"
          />
        </Col>
      </Row>
    </div>
  );
};

export default ActiveProject;
