import React from "react";
import { Col, Row } from "react-bootstrap";
import { IoBody } from "react-icons/io5";

const AboutMe = () => {
  return (
    <div className="mt-3">
      <div class="d-flex align-items-start">
        <IoBody className="fs-1 mt-1" />
        <span className="fs-2 ms-2">About Me</span>
      </div>
      <div className="text-white text-center">
        <Row className="mb-2 mt-2">
          <Col>
            <span className="fs-5">
              <strong>Tools:</strong> Visual Studio Code, Postman, Github
            </span>
          </Col>
          <Col>
            <span className="fs-5">
              <strong>Languages:</strong> Polish (Native), English (B2)
            </span>
          </Col>
        </Row>
        <Row className="mb-2">
          <span className="fs-5">
            <strong>Technologies:</strong> Python, Django, Django REST
            Framework, HTML, CSS, JavaScript, TypeScript, NodeJS, React, Redux,
            Bootstrap, MySQL, PostgreSQL, MongoDB, StyledComponents, SASS
          </span>
        </Row>
      </div>
    </div>
  );
};

export default AboutMe;
