import React from "react";
import { Card } from "react-bootstrap";

const ProjectCard = ({
  title,
  subtitle,
  desc,
  sourceLink = "",
  demoLink = "",
  className,
}) => {
  return (
    <Card className={`h-100 border-white border-1 ${className}`}>
      <Card.Body className="d-flex flex-column">
        <Card.Title>{title}</Card.Title>
        <Card.Subtitle className="text-muted mb-2">{subtitle}</Card.Subtitle>
        <Card.Text>{desc}</Card.Text>
      </Card.Body>
      {sourceLink !== "" && (
        <a href={sourceLink} className="m-2 btn btn-primary mt-auto">
          Source
        </a>
      )}
      {demoLink !== "" && (
        <a href={demoLink} className="m-2 btn btn-primary mt-auto">
          Demo
        </a>
      )}
    </Card>
  );
};

export default ProjectCard;
