import React from "react";
import ProjectCard from "../Home/ProjectCard";

const ActiveProjects = () => {
  return (
    <div>
      <h1 className="text-center">Active Projects</h1>
      <ProjectCard
        title="Tcopy"
        subtitle="Python, Django, Django REST Framework, Django CORS Headers, Django REST Knox"
        desc="Tcopy is Twitter like app - backend"
        sourceLink="https://github.com/Blatts12/tcopy"
        className="mb-3"
      />
      <ProjectCard
        title="Tcopy-web"
        subtitle="NodeJS, React, Redux, Redux Toolkit, React Router, Styled Components, React Use, React Virtuoso"
        desc="Tcopy is Twitter like app - frontend"
        sourceLink="https://github.com/Blatts12/tcopy-web"
      />
    </div>
  );
};

export default ActiveProjects;
